import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MenuPage } from './menu';
 
import { ViewMenuPage } from "./components/View Menu/viewMenu";
import { TabMenuPage  } from "./components/tabMenu/tabMenu";
import { BikeMenuPage } from './components/bike Menu/bikeMenu';
// import { WelcomePage } from '../welcome/welcome';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'cars', // Default sub-route for 'menu'
    pathMatch: 'full',
  },
  {
    path: 'cars',
    component: ViewMenuPage, // Maps to /menu/cars
  },
  {
    path: 'bike',
    component: BikeMenuPage, // Maps to /menu/bike
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MenuPageRoutingModule { }
